.gallery-list {
  .list();
}

.gallery-list__item {
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 338px;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-width: @sm-width) {
      height: 517px;
    }

    @media (min-width: @ml-width) {
      height: 555px;
    }

    @media (min-width: @lg-width) {
      height: 539px;
    }
  }
}

.gallery-list__item--oil-champion::before {
  .gallery-background("blue-back");
}

.gallery-list__item--cosmetics-felix::before {
  .gallery-background("grey-back");
}

.gallery-list__item--oil-felix::before {
  .gallery-background("yellow-back");
}

.gallery-list__item-wrapper {
  position: relative;
  overflow: hidden;

  display: grid;
  grid-template-rows: 1fr;

  box-sizing: border-box;
  min-height: 299px;
  .wrapper-inner();
  padding-top: 27px;
  padding-bottom: 27px;

  @media (min-width: @sm-width) {
    padding-top: 40px;
    padding-bottom: 39px;
  }

  @media (min-width: @lg-width) {
    grid-template-columns: 1fr 1fr;
  }
}

.gallery-list__item-content {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  width: min-content;

  height: 100%;

  @media (min-width: @sm-width) {
    width: 360px;
  }

  @media (min-width: @ml-width) {
    padding-left: 10px;
  }

  @media (min-width: @lg-width) {
    padding-left: 0;
  }
}

.gallery-list__item-description {
  display: flex;
  align-items: center;

  margin: 0;

  color: @color-white;

  font-size: 36px;
  line-height: 40px;
  font-weight: 700;

  @media (min-width: @sm-width) {
    font-size: 42px;
    line-height: 49px;
  }
}

.gallery-list__item-btn {
  .button();

  justify-self: start;

  padding-right: 55px;

  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  text-align: left;
  text-transform: uppercase;

  background-image: url("../../img/more.svg");
  background-size: 39px 22px;
  background-repeat: no-repeat;
  background-position: right center;

  .interactive-styles();
}

.gallery-list__item-slogan {
  display: none;

  @media (min-width: @lg-width) {
    justify-self: end;

    display: block;
    padding: 0;
    padding-top: 102px;
    margin: 0;

    color: @color-white;

    font-size: 75px;
    line-height: 72px;
    font-weight: 300;
  }
}

.gallery-list__item-img {
  position: absolute;
  top: 15px;
  left: 0;
  right: 0;
  z-index: -1;

  margin: 0 auto;

  object-fit: cover;
}

.gallery-list__item--oil-champion .gallery-list__item-img {
  top: 27px;
  width: 191px;
  height: 298px;
  margin-left: 46.6%;

  @media (min-width: @sm-width) {
    top: 15px;
    width: 297px;
    height: 464px;
    margin-left: 49.2%;
  }

  @media (min-width: @ml-width) {
    margin: 0 auto;
  }
}

.gallery-list__item--cosmetics-felix .gallery-list__item-img {
  width: 394px;
  height: 352px;
}

.gallery-list__item--oil-felix .gallery-list__item-img {
  width: 269px;
  height: 330px;
}
