.gallery {
  .wrapper-outer();
}

.gallery__slider {
  position: relative;
}

.gallery__controls {
  position: absolute;
  top: 0;
  right: 0;

  display: flex;
  justify-content: end;

  @media (min-width: @sm-width) {
    right: @sm-padding-inner;
  }

  @media (min-width: @lg-width) {
    right: @lg-padding-inner;
  }

  @media (min-width: @xx-width) {
    right: @xx-padding-inner;
  }
}
